@import '../../../styles/settings.scss';

.button {
  background: transparent;
  border: 1px solid;
  color: $color-text-light;
  text-align: center;
  line-height: 1.3;
  padding: $base-size/2 $base-size;
  margin: $base-size $base-size $base-size $base-size;
  border-radius: $radius;
  cursor: pointer;
  opacity: 0.7;
  transition: $transition;

  &--primary {
    color: $color-primary;
  }

  &--confirm {
    background-color: $color-secondary;
    color: $color-text-bright;
    &:hover {
      box-shadow: -5px -5px 20px rgba(0, 0, 0, 0.1);
      opacity: 1;
    }
  }

  &--info {
    color: $color-info;
  }

  &--danger {
    color: $color-danger;
  }

  &--error {
    color: $color-error;
  }

  &:hover {
    box-shadow: -5px -5px 20px rgba(0, 0, 0, 0.1);
    opacity: 1;
  }
}
